
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.mc-main {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.mc-left,
.mc-right {
  height: 100%;
}
.mc-left {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 200px;
          flex: 0 1 200px;
}
.mc-right {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 100%;
          flex: 1 1 100%;
  overflow: hidden;
}
.mc-nav-wrapper {
  min-height: 100%;
}
.mc-nav {
  height: 100%;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: .25s;
          transition-duration: .25s;
}
.fade-enter-active {
  -webkit-transition-delay: .25s;
          transition-delay: .25s;
}
.fade-enter, .fade-leave-active {
  opacity: 0
}



